@import url(https://fonts.googleapis.com/css?family=Droid+Serif);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{margin:auto;text-align:center;display:flexbox;align-content:center;flex-wrap:wrap;font-family:'Droid Serif';background:#000}.App *{box-sizing:border-box;border-radius:5px}.matrix{width:100%;background:#141313;margin:auto;margin-top:2%}.gen{color:#fff;margin-bottom:10px;text-align:center;font-weight:bold}.board{height:90vh;width:90vh;margin:auto}.row{height:3%;display:flex}.cell{width:3%;margin:0.5%;border-radius:5px}.dead{background:#FFFFFF}.stageOne{background:#300fd8}.stageTwo{background:#b3a7ec}.stageThree{background:#b3a7ec}.stageFour{background:#b3a7ec}.stageFive{background:#b3a7ec}.buttons{background-color:#000;display:flexbox;justify-content:center;padding:1em 1.2em;border:0.1em solid #FFFFFF;border-radius:5px;box-sizing:border-box;text-decoration:none;font-family:'Roboto',sans-serif;font-weight:900;color:#f5f4f8;text-align:center;transition:all 0.2s;margin-bottom:20px}.btn{margin:10px}.loop{color:#fff}.interval{display:flexbox;justify-content:center;border-radius:5px;box-sizing:border-box;text-decoration:none;font-family:'Roboto',sans-serif;font-weight:500;color:#FFFFFF;text-align:center;transition:all 0.2s;margin-bottom:15px}.rules{width:90%;margin:2rem;color:#ffffff}.rules h1{font-size:2rem}.rules ul{list-style:none}.rules li{font-size:1.2rem;margin-bottom:5%;background:#2a9d9031;border-radius:10px;padding:2%}.rules footer{color:#0e2a3a;width:100%;margin:2% 1.25%}.rules footer ul{list-style:none;display:flex;justify-content:space-evenly}.rules footer ul li{border:2px solid #0e2a3a;padding:.5% 1%}.rules footer ul a{text-decoration:none;color:#0e2a3a}

